import {
  GET_SUBSCRIPTION_LIST_SUCCESS,
  SET_SELECTED_SUBSCRIPTION,
  GET_ACTIVE_SUBSCRIPTION_REQUEST,
  GET_ACTIVE_SUBSCRIPTION_SUCCESS,
  GET_ACTIVE_SUBSCRIPTION_FAILURE,
  GET_FEATURE_USAGE_REQUEST,
  GET_FEATURE_USAGE_SUCCESS,
  GET_FEATURE_USAGE_FAILURE,
  GET_SELLER_SUBSCRIPTION_LIST_REQUEST,
  GET_SELLER_SUBSCRIPTION_LIST_SUCCESS,
  GET_SELLER_SUBSCRIPTION_LIST_FAILURE,
  POST_BUY_ADDITIONAL_PLAN_REQUEST,
  POST_BUY_ADDITIONAL_PLAN_SUCCESS,
  POST_BUY_ADDITIONAL_PLAN_FAILURE,
} from "./constants";

const initialState = {
  subscriptionPlanList: [],
  selectedSubscription: {},
  sellerSubscriptionList: [],
  isSubscriptionLoading: false,
  isSubscriptionError: false,
  activeSubscription: {},
  featuresData: [],
};

export default function subscriptionPlanReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case GET_SUBSCRIPTION_LIST_SUCCESS:
      return {
        ...state,
        subscriptionPlanList: payload.data.plans,
      };

    case GET_SELLER_SUBSCRIPTION_LIST_REQUEST:
      return {
        ...state,
      };
    case GET_SELLER_SUBSCRIPTION_LIST_SUCCESS:
      return {
        ...state,
        sellerSubscriptionList: payload.data,
      };
    case GET_SELLER_SUBSCRIPTION_LIST_FAILURE:
      return {
        ...state,
      };
    case SET_SELECTED_SUBSCRIPTION:
      return {
        ...state,
        selectedSubscription: payload,
      };

    // ------------------------- these 2 api calls are called sequentially
    case GET_ACTIVE_SUBSCRIPTION_REQUEST:
      return {
        ...state,
        isSubscriptionLoading: true,
      };

    case GET_ACTIVE_SUBSCRIPTION_SUCCESS:
      return {
        ...state,
        activeSubscription: payload.data.plan,
        isSubscriptionLoading: false,
        isSubscriptionError: false,
        featuresData: [],
      };

    case GET_ACTIVE_SUBSCRIPTION_FAILURE:
      return {
        ...state,
        isSubscriptionLoading: false,
        isSubscriptionError: true,
      };

    //---------------------------------
    case GET_FEATURE_USAGE_REQUEST:
      return {
        ...state,
        isSubscriptionLoading: true,
      };

    case GET_FEATURE_USAGE_SUCCESS:
      return {
        ...state,
        isSubscriptionLoading: false,
        isSubscriptionError: false,
        featuresData: [...state.featuresData, payload.data.usage],
      };

    case GET_FEATURE_USAGE_FAILURE:
      return {
        ...state,
        isSubscriptionLoading: false,
        isSubscriptionError: true,
      };

    default:
      return state;
  }
}
